<template>
    <div class="sort-by-block" :class="{'slideup':sort.show}">
        <div class="txt">
            <svg class="mr8">
                <use xlink:href="@/assets/img/mob-sprite.svg#sort2"></use>
            </svg>
            Сортировать по:</div>
        <div class="sort-by-list">
            <div v-for="item in sort.order"
                 :class="[sortByClass,{active: sort.current.order === item.code}]"
                 @click="setSort(item.code,item.name)"
                 class="sort-item" :key="item.code">
                <a class="hf" href="javascript:void(0)">
                <svg class="icon-check">
                    <use xlink:href="@/assets/img/mob-sprite.svg#check"></use>
                </svg><span>{{ item.name }}</span></a>
            </div>
        </div>
    </div>
    <!--<div class="sort mt16">
        <div class="sort__left" v-if="couponsCount === 1 && !isLoad">
            <div class="text-color-grey text-size-xs text-bold">ЗДЕСЬ ТОЛЬКО ОДИН КУПОН. ВЫБИРАЙТЕ.</div>
        </div>

        <div class="sort__left" v-else-if="!isLoad || isReSort">
            <span class="text">Сортировать по:</span>
            <span v-for="item in sort.order"
                  :class="[sortByClass,{active: sort.current.order === item.code}]"
                  @click="setSort(item.code)"
                  class="sort__link">
                <span>{{ item.name }}</span>
            </span>
        </div>

        <div class="sort__left" v-else></div>

        <div class="sort__right">
            <span class="geo-catalog-link hover" ref="geoCatalogLink" @click="toggleMap">
                <span class="i i-marker"></span>
                <span class="t"></span>
            </span>
        </div>
    </div>-->
    <!-- end sort -->
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        name: "CatalogSort",
        props: {
            favorites: {
                default: function () {
                    return false
                }
            },
            isLoad: {
                default: function () {
                    return false
                }
            }
        },
        computed: {
            sortByClass() {
                return this.sort.current.by.toString() === this.sort.by.asc ? 'up': 'down'
            },
            ...mapState({
                sort: state => state.actions.sort,
                couponsCount: state => state.actions.coupons.length,
            })
        },
        data(){
            return {
                isReSort: false,
            }
        },
        methods: {
            toggleMap: function () {
                this.$store.commit('actions/toggleMap');
            },
            setSort(sort,name) {
                this.isReSort = true;
                this.$store.commit('actions/setShowSort',false);
                document.getElementsByTagName('body')[0].classList.remove('nonscroll');
                if(sort === this.sort.current.order) {
                    this.$store.commit('actions/setSort', {order: sort, by: this.sort.current.by === this.sort.by.desc ? this.sort.by.asc : this.sort.by.desc,name: name});
                } else {
                    this.$store.commit('actions/setSort', {order: sort, by: this.sort.by.desc, name: name});
                }
                this.$store.dispatch('actions/initialCouponState');
                this.$store.dispatch('actions/getCouponList', {
                    currentSection: this.$route.params.sid,
                    reload: true,
                    isSearch: this.$route.path === '/search',
                    favorites: this.favorites
                }).then(()=>{
                    this.isReSort = false;
                });
            },
            hideSort(){
                this.$store.commit('actions/setShowSort',false)
            }
        },
    }
</script>
