<template>
    <div ref="geoCatalogContainer" class="geo-catalog-container">
        <transition name="slide">
            <div v-if="showMap" class="geo-catalog" data-direction="bottom">
                <div class="geo-catalog__content" style="max-height: 502px; overflow: hidden;">
                    <yandex-map
                            :coords="coords"
                            :clusterOptions="claster"
                            :viewAllMarkers="viewAllMarkers"
                            :zoom="zoom"
                            :controls="['zoomControl']"
                            style="width: 100%; height: 430px;"
                            :placemarks="placemarks"
                            map-type="map"
                            @map-was-initialized="initHandler"
                    >
                        <ymap-marker v-for="marker in markers" :key="marker.id"
                                :marker-id="marker.id"
                                :marker-type="marker.type"
                                :cluster-name="'all'"
                                :coords="marker.coords"
                                :hint-content="marker.hint"
                                :balloon="marker.balloon"
                                :icon="map.placemark.icon"
                                :balloonTemplate = "balloonTemplate"
                                :options="{
                                    balloonShadow: map.placemark.balloonShadow,
                                    closeButton: map.placemark.closeButton,
                                    hideIconOnBalloonOpen: map.placemark.hideIconOnBalloonOpen
                                }"
                                :properties="{
                                    coupon: marker.coupon
                                }"
                        ></ymap-marker>
                    </yandex-map>
                </div>

                <span class="geo-catalog__close" @click="toggleMap">
                    <i class="i i-close"></i>
                </span>
                
                <div class="geo-catalog__warning">
                    <span class="icon">
                        <span class="i i-info"></span>
                    </span>
                    
                    <span class="t">На карте показаны только купоны, по которым для получения скидки нужно куда-то ехать. <span v-if="countOther">Помимо них в этой категории есть ещё {{ countOther }} шт.</span></span>
                </div>
            </div>
            <!-- end b-show -->
        </transition>
    </div>
</template>

<script>
    export default {
        name: "CatalogMap",
        props: {
            coords: {
                default: function () {
                    return [56.185272, 43.547612]
                }
            },
            viewAllMarkers: {
                    type: Boolean,
                    default: false
                },
            countOther: {
                default: 0
            },
            placemarks: {
                default: function () {
                    return [/*{
                        coords: [54.8, 39.8],
                        properties: {}, // define properties here
                        options: {}, // define options here
                        clusterName: '1',
                        balloonTemplate: '<div>"Your custom template"</div>',
                        callbacks: { click: function() {} }
                    }*/]
                }
            },
            markers: {
                default: function () {
                    return [/*{
                        id: '1',
                        type: 'placemark', // define properties here
                        coords: [54.7, 39.7], // define options here
                        hint: "Hint content 1",
                        balloon: {header: 'header', body: 'body', footer: 'footer'},
                        cluster: '1',
                        coupon: {
                            image: 'https://zemskidki.ru/upload/skidki/provider/52f/52f084630df8bb35a5ea7b7fa677b66e.jpg',
                            name: 'Отделка лоджий и балконов',
                            id: 69
                        }
                    }*/]
                }
            }
        },
        data: function() {
            return {
                map: {
                    init: false,
                    placemark: {
                        icon: {
                            layout: "default#image",
                            imageHref: require('@/assets/img/baloon-stat.png'),
                            imageSize: [30, 30],
                            imageOffset: [-12, 5],
                            contentOffset: [0, 0],
                            contentTemplate: '<div style="color: #FFFFFF;position:relative;top:-2px;font-weight: bold;">$[properties.geoObjects.length]</div>'
                        },
                        // Балун не имеет тени
                        balloonShadow: false,
                        closeButton: true,
                        hideIconOnBalloonOpen: false
                    }
                },
                claster: {
                    all: {
                        clusterIcons: [
                            {
                                href: require('@/assets/img/baloon-stat.png'),
                                size: [60, 60],
                                offset: [-20, -20]
                            },
                            {
                                href: require('@/assets/img/baloon-stat.png'),
                                size: [60, 60],
                                offset: [-30, -30]
                            }],
                        // Эта опция отвечает за размеры кластеров.
                        // В данном случае для кластеров, содержащих до 100 элементов,
                        // будет показываться маленькая иконка. Для остальных - большая.
                        clusterNumbers: [100],
                    }
                },
                zoom: 10,
            }
        },
        computed: {
            showMap () {
                return this.$store.state.actions.showMap;
            },
            balloonTemplate() {
                return `
                  <span onclick="window.app.$store.dispatch('actions/goCoupon',{id:'{{ properties.coupon.id }}'})" class="yamap_baloon_link hover" style="background-image: url({{properties.coupon.image}});">
                    <b>{{ properties.coupon.name }}</b>
                  </span>
                `
            }
        },
        methods: {
            toggleMap () {
                this.$store.commit('actions/toggleMap');
            },
            initHandler () {
                this.map.init = true;
                this.setTemplateClaster()
            },
            setTemplateClaster () {
                var MyIconContentLayout = ymaps.templateLayoutFactory.createClass(
                    '<div style="color: #FFFFFF;position:relative;top:-4px;font-weight: bold;">$[properties.geoObjects.length]</div>'
                );
                this.claster.all.clusterIconContentLayout = MyIconContentLayout;
            }
        },
        watch: {
            showMap: function() {
                let geoCatalog = this.$refs.geoCatalogContainer;
                
                if(this.showMap == true) {
                    geoCatalog.classList.add('active');
                    // Velocity(geoCatalog, { opacity: 1 });
                } else {
                    geoCatalog.classList.remove('active');
                    // Velocity(geoCatalog, { opacity: 0 });
                }
            }
        }
        
    }
</script>

<style>
    .yamap_baloon_link {
        background: no-repeat left center;
        -webkit-background-size: 50px;
        background-size: 50px;
        padding-top: 4px;
        padding-left: 65px;
        display: block;
        min-height: 50px;
        color: black !important;
        text-decoration: none;
    }
    
    .slide-enter-active {
      animation: slide-out .4s ease;
    }
    .slide-leave-active {
      animation: slide-out .4s reverse ease;
    }
    @keyframes slide-out {
        0% {
            opacity: 0;
            height: 0;
        }
        
        100% {
            opacity: 1;
            height: 430px;
        }
    }
</style>