<template>
    <div class="coupon" :id="'id'+item.id">
        <router-link @click.native="activeItem(item.id)"
                     :to="{ name: 'catalogDetail', params: { id: item.id }}"
                     class="coupon-href"></router-link>

        <div class="coupon-img" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+(item.image_mobile ? item.image_mobile : item.image)+')' }">
            <div class="coupon-stat">
                <div class="views stat-item">
                    <svg class="icon-views">
                        <use xlink:href="@/assets/img/mob-sprite.svg#views"></use>
                    </svg>
                    <span class="count">{{ item.showCounterFormat }}</span>
                </div>
                <div class="purchases stat-item" v-if="item.statistics.sold > 0">
                    <svg class="icon-cart">
                        <use xlink:href="@/assets/img/mob-sprite.svg#cart"></use>
                    </svg>
                    <span class="count">{{ item.statistics.sold }}</span>
                </div>
                <!--<div class="comments stat-item" v-if="item.countOpinions > 0">
                    <svg class="icon-comment">
                        <use xlink:href="@/assets/img/mob-sprite.svg#comment"></use>
                    </svg>
                    <span class="count">{{  item.countOpinions }}</span>
                </div>-->
            </div>
            <!--<div class="rating-stars">
                <svg class="star" v-for="i in 5" :class="{active: i <= 3}">
                    <use xlink:href="@/assets/img/mob-sprite.svg#star"></use>
                </svg>
            </div>-->
        </div>
        <div class="discount-flag" @click="openAverageCheck()">
            <span class="percent">−{{ item.discountFrom }}%</span>
            <span class="text-size-s text-normal sum">≈{{item.realSavingsFormat}} Р</span>
        </div>
        <div class="c-favourite" ref="favoriteBtn" @click="setFavorite(item)">
            <svg :class="{'icon-fav': true, 'fav': true, 'active': item.favorite}">
                <use xlink:href="@/assets/img/mob-sprite.svg#fav"></use>
            </svg>
        </div>
        <div class="coupon-name">{{ item.name }}</div>
        <div class="top">
            <div class="company">{{ item.provider.name }}</div>
        </div>
    </div>

</template>

<script>
    import {flyToFavorite} from '@/helpers/animate';
    import Tooltip from '@/components/Tooltip.vue';
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue'
    import {mapState} from 'vuex';

    export default {
        name: "CatalogListItem",
        components: {
            Tooltip,
            PopupInfoBlock
        },
        data: function() {
            return {
                popupStatItems: {}
            }
        },
        props: {
            item: {
                default: function () {
                    return {}
                }
            },
            isModerator: {
                default: function () {
                    return false
                }
            },
            isSimilar: {
                default: function () {
                    return false
                }
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
            }),
        },
        methods: {
            openAverageCheck() {
                this.$emit('popup');
            },
            activeItem: function (id) {
                if (id) {
                    this.$store.dispatch('actions/goCoupon', {id: id});
                }
            },
            setFavorite: function (item) {
                let action = {...item};
                if (this.isSimilar) {
                    action.isSimilar = true;
                }
                this.$store.dispatch('actions/setFavorite', action);
            },
        }
    }

</script>
