<template>
    <div>
        <popup-info-block
                :show="popups.averageCheck.show"
                :is-set-blank="isBanned"
                :add-class="''"
                :title="''"
                :modal-type="''"
                :text="''"
                :btntext="'Понятно'"
                :name="popups.averageCheck.name">
            <div class="modal-txt">
                <svg class="icon-certif">
                   <use xlink:href="@/assets/img/mob-sprite.svg#certif"></use>
                </svg>
                <p class="txt" style="font-size: 12px;line-height: 15px">
                    При обращении к этому поставщику<br>средний чек составляет <strong>{{ popupData.averageCheckFormat }}
                    Р</strong>,<br>а экономия – около <strong>{{ popupData.realSavingsFormat }} Р</strong>
                </p>
            </div>
        </popup-info-block>

        <div id="catalog-list" class="coupon-list-block coupon-list-main" v-if="!isEmptyLoad || !init">
            <div class="coupon-list-inner inner">
                <div class="breadcrumbs" v-if="$route.params.sid && breadcrumbs.length > 0"><span v-for="(breadcrumb, index) in breadcrumbs"><span v-if="index > 0">/ </span><a href="javascript:void(0)" @click="activeSection(breadcrumb.id)">{{breadcrumb.name}} </a></span></div>
                <div class="list-name" :class="{'solo': favorites}">
                    <span v-if="$route.name === 'home'">
                        <span class="txt">Все скидки </span><a class="choose-city" @click="showPopup('citySelect')">г.&nbsp;{{currentCity.name}}</a>
                    </span>
                    <span v-else>
                        <span class="txt" v-if="title">{{ title }}</span><span class="txt" v-else>&nbsp;</span>
                    </span>
                    <div class="sort" v-if="!favorites" @click="showSort()">
                        <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#sort"></use>
                        </svg>
                    </div>
                </div>
                <div class="coupon-list">
                    <catalog-list-item @popup="showInfo(coupon)"
                                        v-for="coupon in coupons"
                                       :item="coupon"
                                       v-if="((favorites && coupon.favorite) || !favorites) && !(visited && +coupon.id === +$route.params.id)"
                                       :key="coupon.id">
                    </catalog-list-item>
                    <div class="ghost-coupon" v-if="loadAction || !init">
                        <div class="top">

                        </div>
                        <div class="ghost-flag">
                            <svg class="icon">
                                <use xlink:href="@/assets/img/mob-sprite.svg#ghost"></use>
                            </svg>
                        </div>
                        <div class="elements">
                            <div class="">

                            </div>
                            <div class="">

                            </div>
                            <div class="">

                            </div>
                        </div>
                    </div>
                </div>
                <loader v-if="false && (loadAction || !init)" class=""></loader>
            </div>
        </div>
        <div class="nothing-found" v-if="isEmptyLoad && !favorites">
            <div class="txt">К сожалению, по вашему запросу<br>ничего не найдено...</div><router-link to="/catalog" class="link" >смотреть все скидки г.&nbsp;{{currentCity.name}}</router-link>
        </div>
        <div class="no-favourites" v-if="isEmptyLoad && favorites">
            <div class="list-name solo">
                <span>
                    <span class="txt">Избранное</span>
                </span>
            </div>
            <svg class="icon-fav fav">
                <use xlink:href="@/assets/img/mob-sprite.svg#no-fav"></use>
            </svg>
            <div class="nofav">У вас нет избранных купонов</div>
            <div class="addthem">Чтобы они были – добавьте их...</div>
            <div class="button-block icon-big">
                <button class="btn" @click="goHome()">
                    <svg class="icon long-ticket">
                        <use xlink:href="@/assets/img/mob-sprite.svg#long-ticket"></use>
                    </svg>
                    <div class="txt">Смотреть все скидки</div>
                </button>
            </div>
        </div>
        <div @click="hideSort" :class="{'fadeIn':isShowSort}" class="search-inpt-bg"></div>
        <catalog-sort></catalog-sort>
    </div>

</template>

<script>
    import { mapState } from 'vuex'
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue'
    import CatalogListItem from '@/components/catalog/CatalogListItem.vue'
    import CatalogMap from '@/components/catalog/CatalogMap.vue'
    import CatalogSort from '@/components/catalog/CatalogSort.vue'
    import MainTitle from '@/components/MainTitle.vue'
    import Loader from '@/components/Loader.vue';
    import {scrollTo} from '@/helpers/animate.js';

    export default {
        name: "CatalogList",
        components: {
            PopupInfoBlock,
            CatalogListItem,
            CatalogMap,
            CatalogSort,
            MainTitle,
            Loader
        },
        props: {
            favorites: {
                default: function () {
                    return false
                }
            },
            visited: {
                default: function () {
                    return false
                }
            },
        },
        created () {
            this.init = false;
            if(!(this.favorites && this.favoritesItems.length === 0 ) && !(this.visited && this.visitedItems.length === 0 ) || !this.favorites && !this.visited) {
                this.$store.dispatch('actions/getCouponList', {
                    currentSection: this.currentSection,
                    favorites: this.favorites,
                    visited: this.visited,
                    reload: true
                }).then( (e)=> {
                    this.$nextTick(function () {
                        this.init = true;
                    })
                })
            } else {
                this.$store.commit('actions/isLoadNextPage',false)
                this.$nextTick(function () {
                    this.init = true;
                })
            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
        },
        beforeDestroy() {
            this.init = false;
            window.removeEventListener('scroll', this.handleScroll);
        },
        watch: {
            loadAction(after,before){
                let self = this;
                if(this.loadAction == false && this.$store.state.actions.scrollToCoupon){
                    setTimeout(function(){
                        scrollTo('#id'+self.$store.state.actions.couponId,true,-30);
                        self.$store.commit('actions/cancelCouponToScroll');
                    },200);
                }else if(this.loadAction == false && this.$store.state.actions.scrollToCatalogList){
                    setTimeout(function(){
                        scrollTo('#catalog-list',true,-50);
                        self.$store.commit('actions/scrollToCatalogList', false);
                    },200);
                }
            }
        },
        computed: {
            currentSort(){
                return this.$store.getters['actions/getCurrentSort'];
            },
            title () {
                return this.favorites ? 'Избранное' : this.$store.getters['actions/getTitle'](this.currentSection);
            },
            isEmptyLoad () {
                return !this.loadAction && !(this.coupons.length > 0) && this.init;
            },
            currentSection () {
                return this.$route.params.sid;
            },
            markers () {
                let markers = [];
                for(let coupon of this.coupons) {
                    if(coupon.address.length > 0) {
                        for (let addrIndex in coupon.address) {
                            if (!coupon.address[addrIndex].coords) {
                                continue;
                            }
                            let coord = coupon.address[addrIndex].coords.split(',');
                            markers.push(
                                {
                                    id: coupon.id + '-' + addrIndex,
                                    type: 'placemark', // define properties here
                                    coords: [coord[1], coord[0]], // define options here
                                    coupon: {
                                        image: '//api.zemskidki.ru' + coupon.image,
                                        name: coupon.name,
                                        id: coupon.id
                                    }
                                }
                            );
                        }
                    }

                }
                return markers;
            },
            ...mapState({
                loadAction: state => state.actions.isLoad,
                breadcrumbs: state => state.actions.breadcrumbs,
                currentCity:  state => state.cities.current,
                isShowSort:  state => state.actions.sort.show,
                favoritesItems: state => state.actions.favorites.items,
                visitedItems: state => state.actions.visited.items,
                sort: state => state.actions.sort,
                popups: state => state.popups.items,
                isBanned: state => state.auth.isBanned,
            }),
            coupons(){
                return this.favorites ? this.$store.getters['actions/getFavorites']() : this.$store.state.actions.coupons;
            },
            isIndexPage() {
                return this.$route.path === '/'
            },
            currentSortName(){
                return this.sort.order[this.sort.current.order].name
            }
        },
        data(){
            return {
                coords: [],
                init: false,
                popupData: {}
            }
        },
        methods: {
            showInfo: function(item) {
                this.$set(this, 'popupData', {
                    averageCheckFormat: item.averageCheckFormat,
                    realSavingsFormat: item.realSavingsFormat
                });

                console.log(item);

                this.$store.commit('popups/open', {name: this.popups.averageCheck.name});
            },
            goHome: function(){
                this.$router.push('/');
                this.$store.commit('actions/scrollToCatalogList', true);
            },
            handleScroll: function () {
                let D = document;
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = Math.max(
                    D.body.scrollHeight, D.documentElement.scrollHeight,
                    D.body.offsetHeight, D.documentElement.offsetHeight,
                    D.body.clientHeight, D.documentElement.clientHeight
                );

                if(bodyHeight - viewport - 80 <= scroll){
                    this.$store.dispatch('actions/getCouponList', {
                        currentSection: this.currentSection,
                        favorites: this.favorites,
                        visited: this.visited,
                    })
                }
            },
            showPopup(popup) {
                this.$store.commit('popups/open',{name:popup});
            },
            showSort(){
                this.$store.commit('actions/setShowSort',true);
                document.getElementsByTagName('body')[0].classList.add('nonscroll');
            },
            hideSort(){
                this.$store.commit('actions/setShowSort',false);
                document.getElementsByTagName('body')[0].classList.remove('nonscroll');
            },
            activeSection: function (id) {
                if(id){
                    this.$store.commit('actions/setCouponList', {items: []});
                    this.$store.commit('actions/setPage', 0);
                    this.$store.commit('actions/previousSection', id);
                    this.$store.commit('actions/setViewType', 'list');
                    this.$router.push('/sid'+id)
                }
            },
        }

    }
</script>
